.project_card {
  width: 300px;
  padding: 20px;
  background-color: var(--color-white);
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
  cursor: pointer;
  background: rgb(2, 0, 36);
  /* background: rgb(131, 58, 180);
    background: linear-gradient(
      90deg,
      rgba(131, 58, 180, 0.9528186274509804) 18%,
      rgba(253, 29, 29, 0.8323704481792717) 62%,
      rgba(252, 176, 69, 0.9051995798319328) 100%
    ); */
  background-image: url('https://images.unsplash.com/photo-1579548122080-c35fd6820ecb?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80');
}

.project_title {
  font-size: 20px;
  font-weight: 500;
  color: var(--color-white);
}

.project_description {
  font-size: 16px;
  color: var(--color-white);
}

.project_image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 5px;
}

.project_card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}
